<template>
    <v-sheet width="100%" class="page text-center" color="transparent">
        <v-btn
            v-for="(_, index) of 3"
            :key="index"
            class="btn mr-1 px-0"
            :color="index != currentIndex ? 'white' : 'primary'"
            width="8"
            height="8"
            fab
            elevation="0"
            @click="goTo(index)"
        ></v-btn>
    </v-sheet>
</template>
<script>
export default {
    name: "Page",
    components: {},
    props: {
        currentIndex: {
            type: Number,
        },
        total: {
            type: Number,
        },
    },
    data() {
        return {
            parent: this.$parent,
        };
    },
    methods: {
        goTo(index) {
            if (this.currentIndex != index) {
                if (this.parent.clickable === true) {
                    this.parent.goFar(index);
                }
            } else {
                this.parent.onMainSlideClick(index);
            }
        },
    },
};
</script>
<style lang="scss" scoped>
.page {
    position: absolute;
    bottom: 0;
}
</style>
